import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row, Col,Button,Input, Icon,message, Tooltip,Table, Modal, Popconfirm } from 'antd';
import ReactJson from 'react-json-view';
import {CopyToClipboard} from 'react-copy-to-clipboard';

import './index.scss';
// Actions
import { default as QueryActions } from "store/query/actions";
// components
import Loader from 'components/loader';
// import code from 'images/code.svg';

// constants
const APP_CONSTANTS = require('../../constants/app-constants')
const { TextArea } = Input;
const { getQueryData } = QueryActions;
// const Panel = Collapse.Panel;

const tempHistory = [];
class QueryConsole extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading:false,
            showJson:false,
            beautifier:false,
            collapse:false,
            queryResponse:{},
            queryHistory:[],
            showHeader:false,
            visible: false ,
            
        }

        this.runQuery = this.runQuery.bind(this);
        this.clearTextArea = this.clearTextArea.bind(this);
        this.changeView = this.changeView.bind(this);
        this.collapseJson = this.collapseJson.bind(this);
        this.deleteQueryResult = this.deleteQueryResult.bind(this);
        this.copyJson = this.copyJson.bind(this);
        this.copySampleQuery =  this.copySampleQuery.bind(this);
        this.deleteHistory = this.deleteHistory.bind(this);
    }

    componentDidMount(){
        if(null !== localStorage.getItem('query-history')){
             this.tempHistory = JSON.parse(localStorage.getItem('query-history'));
             this.setState({queryHistory:this.tempHistory});
        }else{
         this.tempHistory = [];
        }
         // console.log(JSON.parse(localStorage.getItem('query-history')));
         
     }
 
     componentWillReceiveProps(nextProps){
         console.log(nextProps.queryResult);
         if(nextProps.queryResult && nextProps.queryResult !== this.props.queryResult){
             if(nextProps.queryResult.type !== "GET_QUERY_DATA_ERROR_RESULT"){
                 this.setState({ 
                     queryResponse:nextProps.queryResult,
                     showJson:true,
                     loading:false });
             }else{
                 message.error("Query API Failed");
                 this.setState({loading:false});
             }
         }
     }
 
 
    
   showModal = () => {
     this.setState({
       visible: true,
     });
   }
 
   handleCancel = (e) => {
     // console.log(e);
     this.setState({
       visible: false,
     });
   }
 
     runQuery() {
         debugger;
         this.setState({loading:true});
         var value = document.getElementById('inputquery').value;
         console.log(this.tempHistory);
 
         if(null !== value && value !== ""){
             this.props.getQueryData({
                 "query":value
             })
             
         
                 if(this.tempHistory.length>0){
                     for(var i in this.tempHistory){
                         debugger;
                         if(value !== this.tempHistory[i].query){
                             this.tempHistory.push({
                                 'key':value,
                                 'query':value
                             })
                             
                         }
                     }
                 }else{
                     this.tempHistory.push({
                         'key':value,
                         'query':value
                     })
                 }
           
             this.setState({queryHistory:this.tempHistory},function(){
                 console.log(this.state.queryHistory);
             })
             localStorage.setItem('query-history',JSON.stringify(this.tempHistory));
         }else{
             this.setState({loading:false});
             message.error("Please provide any query");
         }
     }
 
     clearTextArea() {
         document.getElementById('inputquery').value = ""
     }
 
     changeView(){
         this.setState({
             beautifier:!this.state.beautifier
         });
     }
 
     collapseJson(){
         this.setState({
             collapse:!this.state.collapse
         });
     }
 
     deleteQueryResult(){
         document.getElementById('inputquery').value = ""
         this.setState({
             queryResponse:{},
             showJson:false,
             beautifier:false,
             collapse:false,
             loading:false,
             copied:false,
             // queryHistory:[],
             showHeader:false,
         })
     }
 
     copyJson() {
         this.setState({copied: true});
         message.success("Copied to Clipboard");
         // console.log('state'+this.state);
     }
 
     selectedHistory = (record) =>{
         console.log('selected history'+record.query);
         document.getElementById('inputquery').value = "";
         document.getElementById('inputquery').value = record.query;
     }
   
     copySampleQuery () {
         message.success("Copied to Clipboard");
         this.setState({visible: false});
     }
 
     deleteHistory () {
         debugger;
         tempHistory.length = 0;
         console.log(tempHistory);
         this.setState({
             queryHistory:[],
             loading:false,
         })
         localStorage.removeItem('query-history')
         message.success("Deleted Successfully");
     }
 
    
     cancel(e) {
         // message.error('Action cancelled');
     }

     render() {
       
     
        var query_1 =  `{node(func: eq(type,Invoice)) { expand(_all_) }}`;
        var query_2 = `{ node(func: uid(<0x57>)) { expand(_all_) { expand(_all_) }}}`;
        
        return (
            <div className="query-console-page">
               <div className="u_text_right">
                <Button type="primary" onClick={this.showModal}>
                    Example query
                </Button>
                <Modal  title="Example query"
                        visible={this.state.visible}
                        onCancel={this.handleCancel}
                        footer={null}
                        >
                    <div>                        
                        <CopyToClipboard  text={JSON.stringify(query_1).replace(/"|"|;/g,'')} onCopy={this.copySampleQuery}>
                            <div> {JSON.stringify(query_1)}
                                <Tooltip placement="top" title="copy">
                                        <Icon className="query-console-page__icon" type="copy" />
                                </Tooltip>
                            </div>
                        </CopyToClipboard>

                        <CopyToClipboard  text={JSON.stringify(query_2).replace(/"|"|;/g,'')} onCopy={this.copySampleQuery}>
                            <div> {JSON.stringify(query_2)}
                                <Tooltip placement="top" title="copy">
                                        <Icon className="query-console-page__icon" type="copy" />
                                </Tooltip>
                            </div>
                        </CopyToClipboard>
                    </div>
                </Modal>
               </div><br />
               <Row>
                    <Col lg={11} md={11} sm={24} xs={24} className="query-console-page__Col">
                        <div className="query-console-page__inner">
                            {/* <h2>Console</h2> */}
                                <Row className="query-console-page__iconheader">
                                    <Col span={12} className="query-console-page__clear ">
                                        <Button type="danger" onClick={this.clearTextArea}>Clear </Button>
                                    </Col>
                                    <Col span={12} className="query-console-page__run">
                                        <Button type="primary" onClick={this.runQuery}>Run
                                            <Icon type="caret-right" />
                                        </Button>
                                    </Col>
                                </Row>
                                <Row>
                                    <TextArea id="inputquery" rows={8} />
                                </Row>
                        </div><br />
                        <div className="query-console-page__history">
                            {
                                this.state.queryHistory !== undefined && this.state.queryHistory.length>0?
                                <div >
                                    <Row className="query-console-page__iconheader query-console-page__history_header">
                                        <Col span={12} >
                                             <h3>History</h3>
                                        </Col>
                                        <Col span={12} >
                                             <Tooltip placement="top" title="Delete history">
                                                <Popconfirm placement="top" title="Do you want to delete the history?" onConfirm={this.deleteHistory} onCancel={this.cancel} okText="Yes" cancelText="No">  
                                                    <Icon className="query-console-page__icon u_float_right" type="delete"/>
                                                </Popconfirm>
                                             </Tooltip>
                                        </Col>
                                    </Row>
                                   
                                    <Table {...this.state} dataSource={this.state.queryHistory.slice(0, 5)} columns={APP_CONSTANTS.queryHistory} pagination={false}
                                            onRow={(record) => ({ onClick: () => { this.selectedHistory(record); } })}
                                    />
                                </div>
                                :null
                            }
                        </div> <br /> <br />
                    </Col>
                    <Col lg={1} md={1} sm={24} xs={24}/>
                    {
                        !this.state.loading?
                            this.state.showJson?
                                <Col lg={12} md={12} sm={24} xs={24} className="query-console-page__Col">
                                        <div className="query-console-page__inner fadeInright">
                                            {/* <h2></h2> */}
                                            <Row className="query-console-page__iconheader">
                                                <Col span={12} className="query-console-page__responseHeader" >
                                                    <Tooltip placement="top" title={document.getElementById('inputquery').value}>
                                                    {document.getElementById('inputquery').value}
                                                    </Tooltip>
                                                </Col>
                                                <Col span={12} className="query-console-page__run" >
                                                {
                                                    !this.state.collapse?
                                                        <Tooltip placement="top" title="hide">
                                                            <Icon className="query-console-page__icon" type="down" onClick={this.collapseJson}/>
                                                        </Tooltip>
                                                    :
                                                        <Tooltip placement="top" title="show">
                                                            <Icon className="query-console-page__icon" type="up" onClick={this.collapseJson}/>
                                                        </Tooltip>
                                                }
                                                <CopyToClipboard  text={JSON.stringify(this.state.queryResponse)} onCopy={this.copyJson}>
                                                    <Tooltip placement="top" title="copy">
                                                            <Icon className="query-console-page__icon" type="copy" />
                                                    </Tooltip>
                                                </CopyToClipboard>
                                               
                                                {
                                                    !this.state.beautifier?
                                                        // <Tooltip placement="top" title="Beautifier">
                                                        //      <Icon className="query-console-page__icon" type="file-text" onClick={this.changeView}/>
                                                        // </Tooltip>
                                                        <Tooltip placement="top" title="Beautifier">
                                                                <div className="query-console-page__icon"  style={{display:"inline"}} onClick={this.changeView}>
                                                                    <Icon className="query-console-page__beautifier" type="left"/>
                                                                    <Icon className="query-console-page__beautifier" type="right"/>
                                                                </div>
                                                        </Tooltip>
                                                        :
                                                        <Tooltip placement="top" title="Beautifier">
                                                            <div className="query-console-page__icon"  style={{display:"inline"}} onClick={this.changeView}>
                                                                <Icon className="" type="left"/>
                                                                <Icon className="" type="right"/>
                                                            </div>
                                                       </Tooltip>
                                                        // <img className="query-console-page__img" src={code} onClick={this.changeView}/>
                                                        
                                                }
                                                <Tooltip placement="top" title="Delete">
                                                    <Popconfirm placement="top" title="Do you want to delete the console?" onConfirm={this.deleteQueryResult} onCancel={this.cancel} okText="Yes" cancelText="No">  
                                                        <Icon className="query-console-page__icon" type="delete" />
                                                    </Popconfirm>
                                                </Tooltip>
                                              
                                                </Col>
                                            </Row>

                                            {
                                                !this.state.collapse?
                                                    <Row className="query-console-page__jsoncontent">
                                                           
                                                        {
                                                            !this.state.beautifier?
                                                            <div>
                                                                <ReactJson  src={this.state.queryResponse} />
                                                            </div>
                                                        :
                                                            <div>
                                                                {JSON.stringify(this.state.queryResponse)}
                                                            </div>
                                                        }
                                                    </Row>
                                                :null
                                            }

                                        </div>
                                </Col>
                            :null
                        :<Loader />
                    }
                </Row>

            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        ...state,
        queryResult: state.query.QueryData,
    }
}
export default connect(
    mapStateToProps,
    {
        getQueryData
    })(withRouter(QueryConsole));